.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me {
    width: 80%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--primary-color), transparent);
    display: grid;
    place-items:center ;
}

.about__me-image {
    border-radius: 2rem;
    /* overflow: hidden; */
    transform: rotate(10deg);
    transition: var(--transition);
    margin-bottom: 60px;
}

.about__me-image:hover {
    transform: rotate(0deg);
}

.about__me-image img {
    border-radius: 2rem;
    width: 250px;
    position: absolute;
    bottom:-170px;
    left: -130px;
}

.about__cards {
    display: flex;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--bg-color-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
    cursor: pointer;
    width: 220px;
    height: 180px;
}

.about__card:hover {
    border: 1px solid var(--primary-color-variant);
    background: transparent;
}

.about__icon {
    color: var(--primary-color);
    font-size: 2rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.96rem;
}

.about__card small {
    font-size: .7rem;
    color: var(--light-color);
}

.about__content p {
    margin: 2rem 2.6rem;
    color: var(--light-color);
}

.aboutBtn {
    margin-left: 10%;
}

/*! ============== MEDIA QUERIES (SMALL DEVICES) ============== */
@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards {
        /* padding: 0px; */
        display: grid;
        grid-template-columns: max-content;
        gap: 1rem;
        margin-top: 40px;
        transform: translateX(20%);
        width: 100%;
    }
    .about__content {
        text-align: center;
    }
    .about__content p {
        margin: 1.5rem 0;
    }
    .about__container {
        grid-template-columns:1fr;
        gap: 0;
        width: 100%;
    }
}

/*! ============== MEDIA QUERIES (MEDIUM DEVICES) ============== */
@media (min-width:600px) and (max-width: 1024px) {
    .about__cards {
        /* padding: 0px; */
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 0rem;
        /* margin-top: 40px; */
        /* transform: translateX(20%); */
        width: 100%;
    }
    .about__container {
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
        align-items: center;
        width: 100%;
    }
    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    
    .about__content p {
        margin: 1.5rem 0 1.5rem;
    }

}
