header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
    background-image: url("../../assets/wall.jpg");
    background-size: cover repeat;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
    letter-spacing: 2px;
}

.name {
    color: var(--primary-color);
}

.job {
    letter-spacing: 3px;
    font-weight: bold;
    margin-bottom: 0;
    padding-bottom: 0;
}

/*! ============== CTA SECTION ============= */
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    letter-spacing: 0px;
}

/*! ============== HEADER SOCIAL MEDIA ============= */
.header__social {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.3em;
    position: absolute;
    left: 0;
    bottom: 3rem;
    font-size: 20px;
}

.header__social::after {
    content: '';
    width: 1px;
    height: 4rem;
    background: var(--primary-color);
}

.header__social::before {
    content: '';
    width: 1px;
    height: 4rem;
    background: var(--primary-color);
}

/*! ============== ME IMAGE ============= */
.me {
    background: linear-gradient(var(--primary-color), transparent);
    width: 25rem;
    height: 38rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

img {
    width: 700px;
    position: absolute;
    left: -200px;
    bottom: -250px;
}

/*! ============== SCROLL DOWN ============= */
.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 10rem;
    transform: rotate(90deg);
    font-weight: 200;
    letter-spacing: 4px;
    font-size: 0.9re, ;
}

/*! ============== MEDIA QUERIES (MEDIUM DEVICES) ============== */
@media screen and (max-width: 1024px) {
    .header {
        height: 68vh;
    }
}

/*! ============== MEDIA QUERIES (SMALL DEVICES) ============== */
@media screen and (max-width:600px) {
    .header {
        height: 100vh;
    }

    .header__social,
    .scroll__down {
        display: none;
    }
}