.container__portfolio {
    /* height: 100vh; */
    /* width: 100vw; */
    max-height: 800px;
    max-width: 1280px;
    min-height: 600px;
    min-width: 1000px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: 0 auto;
}

.border__portfolio {
    height: 280px;
    width: 380px;
    background: transparent;
    border-radius: 10px;
    transition: border 1s;
    border: 2px solid transparent;
    position: relative;
}

.border__portfolio:hover {
    border: 2px solid var(--primary-color);
}

.card__portfolio {
    height: 300px;
    width: 400px;
    background: #808080;
    border-radius: 10px;
    transition: background 0.8s;
    overflow: hidden;
    background: #000;
    box-shadow: 0 70px 63px -60px #000;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    cursor: pointer;
    margin-top: 3rem;

}

.card0 {
    background: url("../../assets/portfolio1.jpg") center center no-repeat;
    background-size: 400px;
}

.card0:hover {
    background: url("../../assets/portfolio1.jpg") left center no-repeat;
    background-size: 600px;
}

.card0:hover h2 {
    opacity: 1;
}

.card0:hover .fa {
    opacity: 1;
