nav {
    background: rgba(49, 0, 0, 0.19);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(6px);
}

nav a {
    background: transparent;
    padding: .9rem;
    border-radius: 50%;
    display: flex;
    color: var(--white-color);
    font-size: 25px;
    transition: var(--transition);
}


nav a:hover {
    background-color:var(--primary-color);
}
nav a.active {
    background: var(--primary-color);
    color:var(--white-color);
}