footer {
    background: linear-gradient(var(--primary-color), transparent);
    padding: 3rem;
    text-align: center;
    font-size: 2rem;
    margin-top: 7rem;
}

footer a {
    color: var(--light-color);
}

.footer__logo {
    font-size: 3rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
    color: var(--white-color);
    transition: var(--transition);
}
.footer__logo:hover {
    letter-spacing: 3px;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;

}


.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a {
    background: var(--bg-color);
    color: var(--primary-color-variant);
    padding: .8rem;
    border-radius: 1rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: transparent;
    color: var(--white-color);
    border-color: var(--bg-color);
}

.footer__copyright {
    margin-bottom: 4rem;
    color: var(--light-color);
}

/*! ============== MEDIA QUERIES (SMALL DEVICES) ============== */
@media screen and (max-width:600px) {
        .permalinks {
                flex-direction: column;
                gap: 1.5rem;
            }
            .footer__socials {
                margin-bottom: 2.6rem;
            }
}
